@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
}
.navbar{
    height:60px;
    display: flex;
    justify-content: space-between;
    margin: 10px;
    align-items: center;
    border-bottom: grey 1px solid;
    padding-bottom: 10px; 

    box-shadow: 1px 12px 11px -13px #000000;
    position: sticky;
    top: 0;
    z-index: 99;
    background-color: white;
    

}
.logo{font-size: 36px;
    font-family: 'Antonio', sans-serif

}
.left{
    font-size: 36px;
    font-family: 'Antonio', sans-serif;
    /* color:#094f98; */


}
.right{
    display: flex;
    justify-content: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    
}
.badge{
    margin: 10px;
}

.cartItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding: 10px;
    height: 100px;
    margin: 10px 70px;
    border-radius: 20px; 

    box-shadow: 1px 12px 11px -13px #000000;

}
.cartLeft img{
    margin-left:20px ;
    border-radius: 50px;
    width: 90px;
    height: 90px;
}
.cartLeft{
    flex: 1 1;
}
.cartCentre{
    flex: 1 1;
}
.cartRight{
    display: flex;
    justify-content: space-between;
    flex: 2 1;
    width: 15%;
}
.quantity{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
   
}
.quantity button{
    width: 30px;
    height: 30px;
    border-radius: 20px;
    /* border: 0px; */
    outline: 0px;
}
.cartprice{
    width: 100%;
    margin-left: 100px;
    
}
.cancel{
    width: 70px;
    height: 30px;
    border-radius: 20px;
    outline: 1px;
    background-color: rgb(238, 113, 113);
    color: rgb(250, 250, 250);
    font-size: 10px;
    /* border: green; */
    border-color: turquoise;
    opacity: 0.2;
    cursor: pointer;
    position: relative;
    top:-28px;
    left: 20px;
    

}
.cancel:hover{
    opacity: 1;
}

.billing {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
    padding: 10px;
    height: 100px;
    margin: 10px 70px;
    border-radius: 20px; 

    box-shadow: 1px 12px 11px -13px #000000;
}
.empty{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
    padding: 10px;
    height: 100px;
    margin: 10px 70px;
    border-radius: 20px; 

    box-shadow: 1px 12px 11px -13px #000000;    
}
.empty button{
    width: 120px;
    height: 30px;
    background-color: rgb(83, 241, 144);
    cursor: pointer;
    border-radius: 30px;
}
.billing button{
    width: 120px;
    height: 30px;
    background-color: rgb(240, 227, 114);
    cursor: pointer;
    border-radius: 30px;
}
.productwrapper{
    display: flex;
    
    flex-direction:column;
    /* max-width: 30%; */
    align-items: center;
    width: 100%;
    padding: 10px;
    border: 1px solid gray;
    border-radius: 12px;
    margin: 10px;
    word-wrap: break-word;
    flex-wrap: wrap;
}
.productImage{
    /* width: 80%; */
    object-fit: cover;
    /* flex-shrink: 1; */
    width: 100%;
    height: 281px;
    margin-bottom: 20px;

}
.productwrapper h3 {
    text-align: center;
}
.productwrapper button{
    padding: 6px 10px;
    cursor: pointer;
    border-radius: 25px;
    background-color: #ffa430;
    border: none;
    transition: all 0.2s ease-in;
}
.productwrapper button:hover
{
    color: rgb(248, 248, 248);
    background-color: #68f1ad;
    -webkit-transform: translate(2px, -2px);
            transform: translate(2px, -2px);
}
.productwrapper button:focus{
    outline: none;
}
.priceRating{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}
.rating{
    display: flex;
}
.productContainer{ display: grid; 
    grid-template-columns: 1fr 1fr 1fr 1fr; 
    grid-template-rows: auto; 
    grid-gap: 5px 5px; 
    gap: 5px 5px; 
    padding-right: 10px;
  margin-right: 10px;}
      
